body {
  margin: 0;
}

.header {
  background-color: #f3f3f3;
  align-items: baseline;
  gap: 1rem;
  padding: .5rem;
  display: flex;
}

.link {
  color: #777;
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  text-decoration: none;
}

.link:hover {
  color: #000;
}

.dropdown {
  position: relative;
}

.dropdown-menu {
  opacity: 0;
  pointer-events: none;
  background-color: #fff;
  border-radius: .25rem;
  padding: .75rem;
  transition: opacity .16s ease-in-out, transform .16s ease-in-out;
  position: absolute;
  top: calc(100% + .25rem);
  left: 0;
  transform: translateY(-10px);
  box-shadow: 0 2px 5px #0000001a;
}

.dropdown.active > .link + .dropdown-menu {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0);
}

.dropdown.active > .link {
  color: #000;
}

.information-grid {
  grid-template-columns: repeat(2, max-content);
  gap: 2rem;
  display: grid;
}

.dropdown-links {
  flex-direction: column;
  gap: .25rem;
  display: flex;
}

.login-form > input {
  margin-bottom: .5rem;
}

